	module.exports = {"data":{"challengeNode":{"title":"Concatenating Strings with the Plus Equals Operator","description":["我们还可以使用<code>+=</code>运算符来<dfn>concatenate</dfn>（拼接）字符串到现有字符串的结尾。对于那些被分割成几段的长的字符串来说，这一操作是非常有用的。","<strong>提示</strong><br>注意空格。连接操作不会添加两个字符串外面的空格，所以如果想要加上空格的话，你需要自己在字符串里面添加。","<hr>","通过使用<code>+=</code>操作符来连接这两个字符串：<br><code>\"This is the first sentence. \"</code>和<code>\"This is the second sentence.\"</code>并赋给变量<code>myStr</code>。"],"challengeType":1,"fields":{"slug":"/javascript-algorithms-and-data-structures/basic-javascript/concatenating-strings-with-the-plus-equals-operator","blockName":"Basic JavaScript","tests":[{"text":"<code>myStr</code>的值应该是<code>This is the first sentence. This is the second sentence.</code>","testString":"assert(myStr === \"This is the first sentence. This is the second sentence.\", '<code>myStr</code>的值应该是<code>This is the first sentence. This is the second sentence.</code>');"},{"text":"使用<code>+=</code>操作符创建<code>myStr</code>变量","testString":"assert(code.match(/\\w\\s*\\+=\\s*[\"']/g).length > 1 && code.match(/\\w\\s*\\=\\s*[\"']/g).length > 1, '使用<code>+=</code>操作符创建<code>myStr</code>变量');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"// 示例\nvar ourStr = \"I come first. \";\nourStr += \"I come second.\";\n\n// 请把你的代码写在这条注释以下\n\nvar myStr;\n\n","head":"","tail":"(function(){\n  if(typeof myStr === 'string') {\n    return 'myStr = \"' + myStr + '\"';\n  } else {\n    return 'myStr is not a string';\n  }\n})();"},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/javascript-algorithms-and-data-structures/basic-javascript/constructing-strings-with-variables","id":"56533eb9ac21ba0edf2244b8"},"slug":"/javascript-algorithms-and-data-structures/basic-javascript/concatenating-strings-with-the-plus-equals-operator"}}

